/* Import a suitable font */
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@font-face {
  font-family: "Avenir";
  src: url("../assets/fonts/Avenir\ Next\ LT\ Pro\ Regular.otf");
}
/* Resetting margin and padding for the whole page to eliminate any default spacing */
body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-image: url("../assets/images/Potential4.jpg");
}




#big-image {
  position: relative;
  width: 100%;
  height: 200px; /* Fixed height */
  transition: opacity 1s ease-in-out;

}

#big-image.active{
 
  opacity: 1;
}
#banner-image {
  position: relative; /* Changed from absolute to relative */
  width: 100%;

  height: 400px; 
}

#banner-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center 10%;
  transition: opacity 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.home-container {
  font-family: "Avenir", "Playfair Display", serif;

  color: black; /* White text for contrast */
  height: auto; /* Adjusts height based on content */

  text-align: center; /* Centered text */
}

.home-container h1 {
  font-size: 3rem;
  margin-bottom: 0.5em;
  text-shadow: 2px 2px 4px #000000; /* Text shadow for depth */
}

.home-container p,
.home-container i {
 
  margin: 1em 0;
}

p {
  font-size: 1.5rem;
}

.home-container i {
  display: block; /* Making the quote its own block */
  font-style: italic; /* Italicizing quote */
}
.social-media-container {
  margin-right: 2.5rem;
  margin-top: 2.5rem;
  float: right;
  display: flex;
  flex-direction: column;
  align-items: center; /* This will center the links horizontally */
  gap: 10px; /* This will add space between the links */
}

a {
  text-decoration: none;
  color: black;
}
@media screen and ( max-width:768px) {
  .content{
    margin-top: 55px;
  }
  #banner-image{
    height: 200px
  }

  .social-media-container {
    float: none;  /* Clearing any float */
    margin: 0 auto;  /* Centering the container */
    text-align: center;  /* Centering the content inside the container */
    display: flex;  /* Using Flexbox to lay out the icons */
    justify-content: center;  /* Centering icons horizontally */
    gap: 10px;  /* Spacing between the icons */
    flex-direction: row;
  }


}